.comment {
  margin: 0 0 24px 0;
  padding: 0;
  position: relative;
}

.comment::before {
  content: "";
  width: 159px;
  height: 2px;
  background-color: #68b738;
  position: absolute;
  right: -20px;
  top: -47px;
}

.comment__textarea {
  margin: 0;
  border: 1px solid #e7e7e7;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  width: 280px;
  height: 150px;
  padding: 14px;
  box-sizing: border-box;
}
